.App {
  text-align: center;
}

.App-logo {
  height: 20vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 0s linear;
  }
}

.App-header {
  background-color: #282c34;
  height: 25vh;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-body {
  background-color: #20e7ee;
  min-height: 75vh;
  display: block;
  align-items: center;
  justify-content: center;
  font-size: 15px;
  color: rgb(150, 14, 14);
}

.login-page {
  border: 1px solid red;
  width: 250px;
  height: 250px;
  padding: 20px;
  border-radius: 35px;
  background-color: bisque;
}

.login-parent {
  display:block;
  padding: 50px;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
